.grid_container {
    position:absolute;
    min-width:35% ;
    max-width:40%;
    display:flex;
    align-content: flex-start;
}

.card_container {
    width:inherit;
    border : 2px solid ;
    border-top-color:red ;
    border-bottom-color:red;
}

@media (max-width:600px){
    .grid_container {
        min-width:100%;
        align-content: center;
    }
    .card_container {
        min-width: inherit;
    }
}